<template>
  <div>
    <div class="bplayer_start" @click.stop="handleStart" v-if="isStart"></div>
    <div class="bplayer_waiting" v-if="isWaiting && !isStart"></div>
  </div>
</template>

<script>
export default {
  inject: ['$player'],
  props: {
    isStart: {
      default: true,
      type: Boolean
    },
    isWaiting: {
      default: true,
      type: Boolean
    }
  },
  methods: {
    // 开始播放操作
    handleStart() {
      this.$player.handlePlay();
    }
  }
};
</script>

<style lang="stylus" scoped>
.bplayer_start,
.bplayer_waiting
  position absolute
  z-index 39
  top 50%
  left 50%
  transform translate(-50%, -50%)
  background rgba(0, 0, 0, 0.6)
  color #fff
  width 2.5em
  height 2.5em
  border-radius 50%
  cursor pointer
  -webkit-tap-highlight-color transparent
.bplayer_start
  width 4.375em
  height 4.375em
  background url('data:image/png;base64,') no-repeat center center/100%
.bplayer_waiting
  background url('data:image/png;base64,') no-repeat center center/80%
  animation turn 1s infinite linear
@keyframes turn
  0%
    transform translate(-50%, -50%) rotateZ(0deg) rotateY(180deg)
  100%
    transform translate(-50%, -50%) rotateZ(360deg) rotateY(180deg)
</style>
